import React, { Component } from 'react';
import XLSX from 'xlsx';
import { make_cols } from './MakeColumns';


class FileInput extends Component {
    constructor(props) {
        super(props);
        this.state = {
            file: {},
            data: [],
            cols: [],
            fileValue: ""
        }
        this.handleFile = this.handleFile.bind(this);
        this.handleChange = this.handleChange.bind(this);
    }

    refresh() {
        this.setState({
            file: {},
            data: [],
            cols: [],
            fileValue: ""
        })
    }

    handleChange(e) {
        const files = e.target.files;
        if (files && files[0]) this.setState({ file: files[0], fileValue: e.target.value });
    };

    handleFile() {
        /* Boilerplate to set up FileReader */
        const reader = new FileReader();
        const rABS = !!reader.readAsBinaryString;

        reader.onload = (e) => {
            /* Parse data */
            const bstr = e.target.result;
            const wb = XLSX.read(bstr, { type: rABS ? 'binary' : 'array', bookVBA: true });
            /* Get first worksheet */
            const wsname = wb.SheetNames[0];
            const ws = wb.Sheets[wsname];
            /* Convert array of arrays */
            const data = XLSX.utils.sheet_to_json(ws);
            /* Update state */
            this.setState({ data: data, cols: make_cols(ws['!ref']) }, () => {
                this.props.getData(this.state.data)
            });

        };

        if (rABS) {
            reader.readAsBinaryString(this.state.file);
        } else {
            reader.readAsArrayBuffer(this.state.file);
        };
    }

    render() {
        return (
            <div>
                <label htmlFor="file">Upload an excel to Process Triggers</label>
                <br />
                <input type="file" value={this.state.fileValue} className="form-control" id="file" accept={".xls,.xlsx"} onChange={this.handleChange} />
                <br />
                <input type='submit'
                    value="Submit File"
                    onClick={this.handleFile} />
            </div>
        )
    }
}

export default FileInput;